<template>
  <div>
    <div :class="['BINGO_container', 'BINGO_header','pt-4','my-4']">
      <span :class="['bingoLetter']">B</span>
      <span :class="['bingoLetter']">I</span>
      <span :class="['bingoLetter']">N</span>
      <span :class="['bingoLetter']">G</span>
      <span :class="['bingoLetter']">O</span>
    </div>
    <PlayerNameField />
    <CreateRoom />
    <JoinRoom />
    <RoomList />
    <b-modal 
      centered 
      button-size="sm"
      :visible="showJoinRoomModal"
      :hide-header="true"
      :hide-backdrop="true"
      @ok="joinRoomFromLink()"
      @cancel="cancelJoin()"
    >
      Do you want to join this room ?
    </b-modal>
  </div>
</template>
<script>
import CreateRoom from "@/components/CreateRoom.vue";
import JoinRoom from "@/components/JoinRoom.vue";
import RoomList from "@/components/RoomList.vue";
import PlayerNameField from "@/components/PlayerNameField.vue";
import Server from "@/server";
import store from "@/store";
import Emoji from "@/components/svg/Emoji";

export default {
  components: {
    CreateRoom,
    JoinRoom,
    RoomList,
    PlayerNameField
  },
  computed:{
    showJoinRoomModal:()=>store.state.roomIdFromLink!==false
  },
  mounted: function() {
    store.state.mainRoomLoaded = true;
    clearInterval(store.state.timer);
    store.state.room.elapsedTime = 0;
    Server.updateRoomList();
  },methods:{
    joinRoomFromLink(){
      Server.joinBingoRoom(store.state.roomIdFromLink);
      store.state.roomIdFromLink = false;
    },
    cancelJoin(){
      store.state.roomIdFromLink = false;
    }
  }
};
</script>
