<template>
  <div class="settingsWrapper">
    <div class="inner w-100 text-center pb-2">
      <b-button size="sm" @click="leaveRoom()" variant="primary"
        >Leave Room</b-button
      >
    </div>
    <unicon name="cog" />
    <b-modal 
      centered 
      button-size="sm"
      :visible="leaveRoomConfirmModal"
      :hide-header="true"
      :hide-backdrop="true"
      @ok="leaveRoom()"
      @cancel="hideLeaveRoomConfirmModal"
    >
      Are you sure to leave this room ?
    </b-modal>
  </div>
</template>
<script>
import server from "@/server";
import store from "@/store";
export default {
  methods: {
    leaveRoom: () => {
      server.leaveRoom();
    },
    hideLeaveRoomConfirmModal: (e) => {
      e.preventDefault();
      store.state.room.leaveRoomConfirmModal = false;
    }
  },
  computed: {
    gameStarted: () => store.state.room.gameStarted,
    leaveRoomConfirmModal: () => store.state.room.leaveRoomConfirmModal
  }
};
</script>
